<template>
<div>   
        <div class="columns">
            <div class="column is-8">
                <div class="field">
                    <label class="label">[Link]:</label>
                    <p class="control">
                        <input id="url" class="input" name="url" v-model="url" type="text" placeholder="www.blog.com/[LINK]">
                    </p>
                </div>
                <div class="field">
                    <label class="label">Title:</label>
                    <p class="control">
                        <input id="title" class="input" name="title" v-model="title" type="text" placeholder="Title">
                    </p>
                </div>
            </div>
            
            <div class="column is-4">
                <label class="label">Categories:</label>
                <div class="dropdown " :class="{ 'is-active': toggle}">
                    <div class="dropdown-trigger">
                        <button @click="toggleCategory" class="button" aria-haspopup="true" aria-controls="dropdown-menu">
                        <span>{{categoryNow.name}}</span>
                        <span class="icon is-small">
                            <i class="ri-arrow-down-s-line"></i>
                        </span>
                        </button>
                    </div>
                    <div class="dropdown-menu" id="dropdown-menu" role="menu">
                        <div class="dropdown-content">
                        <a @click="changeCategory(category);" v-for="category in categories " :key="category.name" class="dropdown-item">
                            {{category.name}}
                        </a>

                        <hr class="dropdown-divider my-3">
                            <div class="field mx-3">
                                <div class="control">
                                    <input class="input is-small" v-model="addCategoryName" type="text" placeholder="Add...">
                                </div>
                            </div>
                            <button @click="addCategory" class="button is-small ml-3">Add category</button>
                        </div>
                    </div>
                </div>
            
            </div>
        </div>
        <div id="editor">
         <textarea style="display: none;">#HELLO : )</textarea>
        </div>
        <div class="field">
                    <label class="label">Description:</label>
                    <p class="control">
                        <input id="description" class="input" name="description" v-model="description" type="text" placeholder="(Description in the meta, for SEO)">
                    </p>
        </div>
        <button id="getHtml" class="button" @click="postPost">Save</button>
        <button id="recoverDraft" class="button mx-3" @click="recoverDraft">Draft recover</button>
</div>
</template>

<script>
    export default {
        data() {
            return {
                url: '',
                title: '',
                editor: {},
                content : "#test",
                resp: {},    //The post
                req:{},
                categories: '',
                categoryNow: {id:0, name:'(please select)'},
                toggle:false,
                addCategoryName:'',
                timer: '',
                description: ''
            }
        },

        mounted() {
            /* global editormd */
                this.timer = setInterval(this.heartbeat, 60000);
                let self = this;
                    const editor = editormd("editor",{
                    width : "100%",
                    height : "60vh",
                    path :"./static/editor.md-master/lib/",
                    pluginPath : "./static/editor.md-master/plugins/",
                    syncScrolling : "single",
                    emoji :true,
                    saveHTMLToTextarea : true,
                    onchange : function(){self.autoSave()} ,
                    onload: function(){self.afterLoaded()}
                    });
                    this.editor = editor;
        },

        methods: {
            postPost() {
                if (this.$route.name == 'EditPost') {
                    this.req.url = this.url;
                    this.req.title = this.title;
                    this.req.contentMarkdown = this.editor.getMarkdown();
                    this.req.contentHTML = this.editor.getHTML();
                    this.req.categoryId = this.categoryNow.id;
                    this.req.status = 0;
                    this.req.id = this.resp.pid;
                    this.req.description = this.description;
                    
                    this.$axios.post('/postsmgmt/updatepost/',this.req).then( (response) => {
                        if (response.data.status === 200 ) {
                            this.$notify('Done.')
                        } else {
                            this.$notify('Error: '+response.data.msg)
                        }
                    })

                } else {
                    this.req.url = this.url;
                    this.req.title = this.title;
                    this.req.contentMarkdown = this.editor.getMarkdown();
                    this.req.contentHTML = this.editor.getHTML();
                    this.req.categoryId = this.categoryNow.id;
                    this.req.status = 0;
                    this.req.description = this.description;
                    
                    this.$axios.post('/postsmgmt/create/',this.req).then( (response) => {
                        if (response.data.status === 200 ) {
                            this.$notify('Done.')
                        } else {
                            this.$notify('Error: '+response.data.msg)
                        }
                    })
                }
            },

            getPost() {
                this.$axios.get('/postsmgmt/edit/'+this.$route.params.pid).then(resp => {
                    console.log('[*] Received data from server:');
                    console.log(resp.data);
                    this.resp = resp.data.data;
                    console.log(typeof(this.resp));
                    this.title = this.resp.title;
                    this.url = this.resp.url;
                    this.description = this.resp.description;
                    this.editor.setValue(this.resp.content_md);
                    this.categoryNow = this.categories.filter(function(x){return x.id == resp.data.data.category_id})[0];
                    window.edi = this.editor;
                        
                })
            },

            afterLoaded() {
                if (this.$route.name == 'EditPost') {
                    this.getPost();

                }
            },

            autoSave() {
                localStorage['draft_'+ this.url] = this.editor.getMarkdown();
            },

            recoverDraft() {
                if (localStorage['draft_'+this.url]){
                    this.editor.setValue(localStorage['draft_'+this.url]);
                } else {
                    this.$notify('There is no draft.')
                }
            },

            toggleCategory() {
                this.$set(this, 'toggle', !this.toggle);
            },

            changeCategory(category) {
                this.categoryNow = category;
                this.toggleCategory();
            },

            getCategories() {
                this.$axios.get('/postsmgmt/getcategories').then(resp => {
                this.categories = resp.data.data.categories;
                console.log('[*]CATEGORIES:')
                console.log(this.categories);
                })
            },

            heartbeat() {
                this.$axios.get('/admin/ping').then(resp=> {
                    if(resp.data.status == 200){console.log('[*]Heartbeat: Online, session refreshed.')}
                });
            },

            addCategory() {
                this.$axios.post('/postsmgmt/addcategory',{name:this.addCategoryName}).then(resp=>{
                    if(resp.data.status == 200){
                        this.$notify('Success');
                        this.getCategories();
                    }
                })
            }

        },

        created() {
            console.log('[+]CreatePost')
            if (this.$route.name == 'EditPost') {
                //this.getPost();
            }
            this.getCategories();

        },


        beforeDestroy() {
            clearInterval(this.timer);
            console.log("[*]Timer Destroyed");
        },
    }
</script>